<div class="item-list"><div class="title">{{title}}</div><div class="list"> <template v-for="(item, idx) in mutatedList"><div class="text">{{item}}</div><div class="delete"><button @click="deleteItem(idx)"><i class="material-icons">delete</i></button></div></template></div><div class="input"><input type="text" v-model="itemToAdd"><button @click="updateList"><i class="material-icons">add</i><span>Add</span></button></div></div>