<div class="search-results-main"><EntityTable :tableData="formattedResults" title="Search Results" v-slot="{ entities }" v-if="formattedResults.length" :displaySearch="false"> <div class="action-button-holder"><CopyButton :textToCopy="fullUrl" icon="share" buttonText="Shareable Link"></CopyButton><button :disabled="!formattedResults.length" @click="showDownloadResults=true">Download All Search Results</button></div><ReactionCard v-for="row in entities" :reaction="row" :isSelectable="true" :isSelected="selectedReactions.includes(row.reaction_id)" @clickedSelect="updateSelectedReactions"></ReactionCard></EntityTable><transition name="fade"><div class="view-selected-container" v-if="selectedReactions.length"><div class="view-selected-button" @click="goToViewSelected">View {{selectedReactions.length}} selected reactions</div></div></transition><DownloadResults :reactionIds="formattedResults.map(result =&gt; result.reaction_id)" :showDownloadResults="showDownloadResults" @hideDownloadResults="showDownloadResults=false"></DownloadResults></div>