<div class="search-options"><div class="options-title" @click="showReagentOptions = !showReagentOptions" :class="showReagentOptions ? &quot;&quot; : &quot;closed&quot;"> <span>Components</span><i class="material-icons">expand_less</i></div><transition name="expand"><div class="options-container" id="searchByReagent" v-if="showReagentOptions"><div class="section"></div><div class="subtitle"> <div class="tabs"><div class="tab capitalize" v-for="mode in matchModes" @click="reagentOptions.matchMode = mode" :class="reagentOptions.matchMode === mode ? &quot;selected&quot; : &quot;&quot;">{{mode}}</div></div></div><div class="section"><div class="subtitle">General Options</div><div class="general options"><label for="stereo">Use Stereochemistry</label><input id="stereo" type="checkbox" v-model="reagentOptions.useStereochemistry"><template v-if="reagentOptions.matchMode === &quot;similar&quot;"><label for="similarity">Similarity Threshold</label><div class="slider-input"><div class="value">{{simThresholdDisplay}}</div><input id="similarity" type="range" min="0.1" max="1.0" step="0.01" v-model="reagentOptions.similarityThreshold"></div></template></div></div><div class="section"><div class="subtitle">Reactants & Reagents</div><div class="reagent options"><template v-for="(reactant, idx) in reagentOptions.reactants"><div class="draw"><button @click="openKetcherModal(&quot;reactants&quot;, idx)"><i class="material-icons">draw</i></button></div><div class="field long"> <input type="text" v-model="reactant.smileSmart"></div><div class="delete"><button @click="reagentOptions.reactants.splice(idx,1)"><i class="material-icons">delete</i></button></div></template><div class="copy" v-if="!reagentOptions.reactants.length">No components</div><div id="add-component"><button type="button" @click="this.reagentOptions.reactants.push({smileSmart: &quot;&quot;, source: &quot;input&quot;})"><i class="material-icons">add</i> Add Component</button></div></div></div><div class="section"><div class="subtitle">Products</div><div class="reagent options"><template v-for="(product, idx) in reagentOptions.products"><div class="draw"><button @click="openKetcherModal(&quot;products&quot;, idx)"><i class="material-icons">draw</i></button></div><div class="field long"> <input type="text" v-model="product.smileSmart"></div><div class="delete"><button @click="reagentOptions.products.splice(idx,1)"><i class="material-icons">delete</i></button></div></template><div class="copy" v-if="!reagentOptions.products.length">No components</div><div id="add-component"><button type="button" @click="this.reagentOptions.products.push({smileSmart: &quot;&quot;, source: &quot;output&quot;})"><i class="material-icons">add</i> Add Component</button></div></div></div></div></transition><div class="options-title" @click="showReactionOptions = !showReactionOptions" :class="showReactionOptions ? &quot;&quot; : &quot;closed&quot;"> <span>Reactions</span><i class="material-icons">expand_less</i></div><transition name="expand"><div class="options-container" id="searchByReaction" v-if="showReactionOptions"><SearchItemList title="Reaction IDs" :itemList.sync="reactionOptions.reactionIds"></SearchItemList><SearchItemList title="Reaction SMARTS" :itemList.sync="reactionOptions.reactionSmarts"></SearchItemList><div class="slider-input multi"><label for="yield">Yield</label><div class="value">{{reactionOptions.min_yield}}% - {{reactionOptions.max_yield}}%</div><MultiRangeSlider baseClassName="multi-range-slider" :miin="0" :max="100" :step="1" :ruler="false" :label="false" :minValue="reactionOptions.min_yield" :maxValue="reactionOptions.max_yield" @input="updateYield"></MultiRangeSlider></div><div class="slider-input multi"><label for="yield">Conversion</label><div class="value">{{reactionOptions.min_conversion}}% - {{reactionOptions.max_conversion}}%</div><MultiRangeSlider baseClassName="multi-range-slider" :miin="0" :max="100" :step="1" :ruler="false" :label="false" :minValue="reactionOptions.min_conversion" :maxValue="reactionOptions.max_conversion" @input="updateConversion"></MultiRangeSlider></div></div></transition><div class="options-title" @click="showMechanoOptions = !showMechanoOptions" :class="showMechanoOptions ? &quot;&quot; : &quot;closed&quot;"> <span>Mechanochemistry</span><i class="material-icons">expand_less</i></div><transition name="expand"><div class="options-container" id="searchByMechano" v-if="showMechanoOptions"><div class="section"><div class="subtitle">Treatment Type</div><div class="general options"><select id="treatment" type="selected" v-model="mechanoOptions.treatmentType" style="width:90%" multiple><option value="BALL_MILL">Ball Milling</option><option value="AFM">AFM</option><option value="HAND_GRIND">Hand Grinding</option><option value="TWIN_SCREW">Twin Screw</option></select></div></div><div class="section"><div class="subtitle">Treatment Modifiers</div><div class="general options"><label for="liquass" style="margin-right:15px">Liquid Assisted</label><input id="liquass" type="checkbox" v-model="mechanoOptions.liquidAssisted"></div></div></div></transition><div class="options-title" @click="showDatasetOptions = !showDatasetOptions" :class="showDatasetOptions ? &quot;&quot; : &quot;closed&quot;"> <span>Datasets</span><i class="material-icons">expand_less</i></div><transition name="expand"><div class="options-container" id="searchByDataset" v-if="showDatasetOptions"><SearchItemList title="Dataset IDs" :itemList.sync="datasetOptions.datasetIds"></SearchItemList><SearchItemList title="DOIs" :itemList.sync="datasetOptions.DOIs"></SearchItemList></div></transition><div class="options-title" id="searchParameters">Search Parameters</div><div class="options-container"><div class="section"><label for="limit">Result Limit </label><input id="limit" type="number" min="0" v-model="searchParams.limit"></div><div class="search-button"><button @click="emitSearchOptions"><b>Search</b></button></div></div></div><ModalKetcher v-if="showKetcherModal" :smiles="reagentOptions[ketcherModalSet][ketcherModalSmile].smileSmart" @updateSmiles="(newSmiles) =&gt; reagentOptions[ketcherModalSet][ketcherModalSmile].smileSmart = newSmiles" @closeModal="showKetcherModal = false"></ModalKetcher>